import React from 'react';
import ProdictItem from './productItem';
import s from '../../css/popular.module.css'
import ScrollAnimation from 'react-animate-on-scroll';

const Popular = (props) =>{
    
    let result = props.items.filter(item1 => {
        return props.popularProduct.some(item2 => {
            return item1.id === item2.id
        }
    )})

    return( 
        <div className={`${s.section} ${s.devices}`}>
		    <div className={s.container}>
			    <div className={s.headerS1}>
                    <h2 className={s.sectionTitle}>–ü–æ–ø—É–ª—è—Ä–Ω—ã–µ —Ç–æ–≤–∞—Ä—ã</h2>
                    <div className={s.sectionText}>
                        <p className={s.sectionText}>–í –Ω–∞—Å—Ç–æ—è—â–µ–µ –≤—Ä–µ–º—è —ç–∫–æ-–ø—Ä–æ–¥—É–∫—Ç —Å—Ç–∞–ª –±–æ–ª–µ–µ –ø–æ–ø—É–ª—è—Ä–Ω—ã–º –∏ –≤–æ—Å—Ç—Ä–µ–±–æ–≤–∞–Ω–Ω—ã–º. –≠—Ç–æ –ø–æ–º–æ–≥–∞–µ—Ç –∑–∞—â–∏—Ç–∏—Ç—å –Ω–∞—à—É –∑–µ–º–ª—é –æ—Ç –≤—Ä–µ–¥–Ω–æ–≥–æ –ø—Ä–æ–∏–∑–≤–æ–¥—Å—Ç–≤–∞.</p>
                    </div>
                    { 
                    <ScrollAnimation animateIn='fadeIn' animateOut='fadeOut'>
                        <ProdictItem   
                            items={result} 
                            popularProduct={props.popularProduct} 
                                />
                    </ScrollAnimation>
                    }         
                </div>
            </div>
        </div>
    )
}

export default Popular