  let equipmentItems = descriptionTable.equipment.map( equipment => <span key={equipment.id}>{equipment.name}, </span>)
    
  return (
        <div className={s.DescriptionParams}>
        <ul className={s.characteristics}>
          <li className={s.items}>
            <h3 className={s.itemsTitle}>–û–±—â–∏–µ —Ö–∞—Ä–∞–∫—Ç–µ—Ä–∏—Å—Ç–∏–∫–∏</h3>
            <table>
              <tbody>
                <tr className={s.item}>
                  <td className={s.itemLabel}><span>–¢–∏–ø</span></td>
                  <td className={s.itemValue}><p>{data.descriptionTable.typeName}</p></td>
                </tr>
                <tr className={s.item}>
                  <td className={s.itemLabel}><span>–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø–µ—Ä—Å–æ–Ω</span></td>
                  <td className={s.itemValue}><p>{data.descriptionTable.countPeople}</p></td>
                </tr>
                <tr className={s.item}>
                  <td className={s.itemLabel}><span>–û—Å–æ–±–µ–Ω–Ω–æ—Å—Ç–∏</span></td>
                  <td className={s.itemValue}>
                    <p>
                      <span>{data.descriptionTable.features}</span> 
                    </p>
                  </td>
                </tr>
                <tr className={s.item}>
                  <td className={s.itemLabel}><span>–ú–∞—Ç–µ—Ä–∏–∞–ª</span></td>
                  <td className={s.itemValue}>
                    {colorItems}
                  </td>
                </tr>
                <tr className={s.item}>
                  <td className={s.itemLabel}><span>–ö–æ–º–ø–ª–µ–∫—Ç–∞—Ü–∏—è</span></td>
                  <td className={s.itemValue}>
                    <p>
                      {equipmentItems}
                    </p>
                  </td>
                </tr>
                <tr className={s.item}>
                  <td className={s.itemLabel}><span>–≠–∫–æ–ª–æ–≥–∏—á–Ω–æ—Å—Ç—å</span></td>
                  <td className={s.itemValue}><p>{data.descriptionTable.Eco}</p></td>
                </tr>
                
              </tbody>
            </table>
          </li>
        </ul>
      </div>
    )
